body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

main {
  flex: 1 0 auto;
  background: url("./img/cdc-w9KEokhajKw-unsplash.jpg") no-repeat center
    center/cover;
}

.result-square {
  width: 150px;
  height: 150px;
  background-color: white;
  border-radius: 3px;
  border: 1px dotted red;
  text-align: center;
}

.grid-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

h2 {
  text-align: center;
  color: white;
  border-bottom: dotted 1px white;
}

h6 {
  color: #d32f2f;
}

.search-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 300px;
}

input[type="text"]::placeholder {
  text-align: center;
  color: white;
}

input[type="text"] {
  color: white;
}

.btn {
  display: block;
  margin: auto;
}

.fade-in {
  opacity: 1;
  animation-name: fadeInOpacity;
  animation-iteration-count: 1;
  animation-timing-function: ease-in;
  animation-duration: 2s;
}

@keyframes fadeInOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
